.header{
    min-height: 56px;
    display: flex;
    justify-content: center;
    padding-left: 34px;
    gap: 58px;
    align-items: center;
    background: linear-gradient(180deg, #8000FF 0%, #9E00FF 40.42%, #61009C 100%);
    border-bottom: 2px solid #913BFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), inset 0px -11px 19px rgba(56, 0, 112, 0.63);
}

.header__container{
    display: flex;
    align-items: center;
    gap: 80px;
    justify-content: center;
    flex: 2;
}
.header__score-container{
    width: 241px;
    height: 36px;
    display: flex;
   
    align-items: center;
    background: #370063;
    border: 1px solid #FFB800;
    box-shadow: inset 0px 4px 3px rgba(32, 0, 65, 0.91);
    border-radius: 18.5px;

}

.header__score{
    font-family: 'Impact';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.05em;
    background: linear-gradient(180deg, #0066FF 0%, #BD00FF 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.header__money{
    font-family: 'Impact';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.05em;
    background: linear-gradient(180deg, #EBFF00 0%, #FFA800 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.header__button{
    height: 42px;
    width: 42px;
    margin-top: 8px;
    
}

.header__button_inactive{
    opacity: 0;
}

.header__score-img{
    height: 47px;
    width: 49px;
    margin-right: 34px;
}


.header__money-img{
    height: 27px;
    width: 39px;
    margin-left: 10px;
    margin-right: 18px;
}
