.royal-coins{
    background: url(../../images/royal-coins/background.svg);
    background-repeat: no-repeat;
    background-size: cover;
    
}

.slot{
    display: flex;
    padding: 4px 0;
  
}

.slot__container{
    background: rgba(21, 9, 31, 0.73);
    border: 1px solid #FFE600;
    border-radius: 20px;
    display: flex;
    margin: 0 auto;
    gap: 24px;
    overflow: hidden;
}

.slot__column{
    display: flex;
    flex-direction: column;
    gap: 13px;
    /* transition: transform 3s ease-in-out;
    transform:translateY(100px) */
}

.slot__img{
    width: 105px;
    height: 68px;
}