.control-panel{
    min-height: 56px;
    display: flex;
    justify-content: center;
    padding: 0 34px;
    gap: 68px;
    align-items: center;
    background: linear-gradient(180deg, #8000FF 0%, #9E00FF 40.42%, #61009C 100%);
    border-bottom: 2px solid #913BFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), inset 0px -11px 19px rgba(56, 0, 112, 0.63);
 
}

.bid{
    display: flex;
    gap: 13px;
    align-items: center;
    
}

.bid__button{
    margin: 0;
    margin-top: 8px;
    background: none;
    outline: none;
    border: none;
}

.bid__number-container{
    display: flex;
    background: #320950;
    border-radius: 10px;
    padding: 7px 14px;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
}

.bid__number{
    margin: 0;
    font-family: 'Impact';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.05em;
    background: linear-gradient(180deg, #EBFF00 0%, #FFA800 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-shadow: 0px 4px 10px rgba(250, 255, 0, 0.62);
}

.win{
    min-width: 189px;
    min-height: 38px;
    background: #320950;
    border-radius: 10px;
    display: flex;
    align-items: center;
    gap: 37px;
    padding: 7px 14px;
    box-sizing: border-box;
}

.win__text{
    margin: 0;
    font-family: 'Impact';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.05em;
    background: linear-gradient(180deg, #FFFFFF 0%, #1DC9FF 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-shadow: 0px 4px 10px rgba(0, 224, 255, 0.28);
}

.win__number{
    margin: 0;
    font-family: 'Impact';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.05em;
    background: linear-gradient(180deg, #EBFF00 0%, #FFA800 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-shadow: 0px 4px 10px rgba(250, 255, 0, 0.62);
}

.buttons-container{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
}


.buttons-container__auto-button{
    padding: 11px 27px;
display: flex;
align-items: center;
letter-spacing: 0.05em;
background: linear-gradient(180deg, #70FF00 0%, #BD00FF 0.01%, #005484 100%);
border: 1px solid #00A3FF;
box-shadow: 0px 4px 4px rgba(36, 255, 0, 0.27), inset 0px -6px 4px rgba(0, 163, 255, 0.26), inset 0px 4px 4px rgba(255, 247, 207, 0.65);
border-radius: 11px;

}

.auto-button__text{
    margin: 0;
    text-transform: uppercase;
    font-family: 'Impact';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    display: flex;
    align-items: center;
    letter-spacing: 0.05em;
    background: linear-gradient(180deg, #FFFFFF 0%, #1DC9FF 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.buttons-container__spin-button{
    padding: 11px 27px;
    background: linear-gradient(180deg, #70FF00 0%, #008405 100%);
    border: 1px solid #FFB800;
    box-shadow: 0px 4px 4px rgba(36, 255, 0, 0.27), inset 0px -6px 4px rgba(0, 255, 56, 0.26), inset 0px 4px 4px rgba(255, 247, 207, 0.65);
    border-radius: 11px;
}

.spin-button__text{
    margin: 0;
    text-transform: uppercase;
    font-family: 'Impact';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    display: flex;
    align-items: center;
    letter-spacing: 0.05em;
    background: linear-gradient(180deg, #FFFFFF 0%, #73FF1D 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}